body, p {
    font-family: var(--uv-font-family);
    color: var(--uv-font-color);
    font-size: inherit;
    line-height: 20px;
}

.text-disabled {
    color: var(--uv-color-gray-2);
    cursor: not-allowed !important;
}

.uv-body {
    font-family: var(--uv-font-family);
    font-size: var(--uv-font-size-sm);
    line-height: 24px;
    color: var(--uv-font-color);

    &-sm-md {
        font-size: var(--uv-font-size-sm-md);
    }

    &-sm {
        font-size: var(--uv-font-size-xs);
        line-height: 16px;
    }

    &-secondary{
        font-size: var(--uv-font-size-xs);
        color: var(--uv-font-color-secondary);
        line-height: var(--uv-font-size-md);
    }

    &-tertiary {
        font-size: var(--uv-font-size-xs);
        color: var(--uv-color-primary);
        line-height: var(--uv-font-size-md);
    }
}

.color-primary {
    color: var(--uv-color-primary);
}

h1 {
    font-weight: var(--uv-font-semibold);
    font-size: var(--uv-font-size-lg);
    line-height: 36px;
}

h2 {
    font-size: var(--uv-font-size-xl);
    line-height: 40px;
    letter-spacing: -0.1;
    font-weight: 100;
}

h3 {
    font-weight: var(--uv-font-medium-bold);
    font-size: var(--uv-font-size-lg);
    line-height: 36px;
}

// h4 {
//     font-weight: var(--uv-font-medium-bold);
//     font-size: var(--uv-font-size-md-lg);
//     line-height: 32px;
// }

h5 {
    font-weight: var(--uv-font-medium-bold);
    font-size: var(--uv-font-size-md);
    line-height: 24px;
}

.heading {
    &-sub{
        font-size: var(--uv-font-size-md);
        font-weight: var(--uv-font-bold);
        line-height: 20px;
    }
}

.uv-label {
    font-size: var(--uv-font-size-sm-md);

    &-sbs {
        font-size: var(--uv-font-size-xs);

        &-primary {
            font-weight: 600;
        }

        &-secondary {
            font-size: var(--uv-font-size-xs);
            color: var(--uv-font-color-secondary);

            &-active {
                color: var(--uv-color-primary);
            }
        }
    }
}

.uv-placeholder {
    font-size: var(--uv-font-size-sm-md);
    color: var(--uv-color-gray-dark);
    font-style: italic;

    &-sbs {
        font-style: none;
    }
}

.bold {
    font-weight: var(--uv-font-bold);
}

.semibold {
    font-weight: var(--uv-font-semibold);
}

.medium-bold {
    font-weight: var(--uv-font-medium-bold);
}

.italic {
    font-style: italic;
}

.uv-font-gray {
    color: var(--uv-color-gray-4)
}

.highlight {
    background-color: var(--uv-color-gray-light);
}

.uv-link,
a,
a:link,
a:hover,
a:active,
a:visited {
    font-size: var(--uv-font-size-sm);
    font-weight: var(--uv-font-bold);
    color: var(--uv-color-primary);
    text-decoration: none;
    cursor: pointer;

    &-secondary {
        color: gray;
    }

    &:hover {
        color: var(--uv-color-primary-active);
    }
}

ul, ol {
    list-style-position:inside;
}

.hover-blue {
    &:hover {
        color: var(--uv-color-primary);
    }
}

.uv-input, input {
    font-size: var(--uv-font-size-sm);
    font-family: inherit;
    border: none;
    height: 100%;
    width: 100%;

        &:focus,
        &:invalid {
            outline: none;
        }
    
        &-label {
            font-weight: var(--uv-font-bold);
            font-size: var(--uv-font-size-xs);
            color: var(--uv-color-gray-dark);
            line-height: 28px;
        }
    &-border {
        border: var(--uv-border-gray);
        padding: 4px 8px;
        border-radius: 4px;
    }
}

::-webkit-input-placeholder {
    color: var(--uv-color-gray-dark);
    font-style: italic;
    font-size: var(--uv-font-size-sm-md);
}

.custom-option {
    // position: relative;
    // display: block;
    padding: 8px 12px;
    cursor: pointer;
}
.custom-option:hover {
    cursor: pointer;
    background-color: var(--uv-color-gray-light);
}

.custom-option.selected {
    // color: #ffffff;
    // background-color: #305c91;
}

.results-number {
    border: var(--uv-border-gray);
    border-radius: 4px;
    color: var(--uv-color-gray-dark);
    font-weight: var(--uv-font-bold);
    padding: 2px 10px;
    font-size: var(--uv-font-size-sm);

    &-sm {
        padding: 4px 8px;
        font-weight: var(--uv-font-semibold);
        font-size: var(--uv-font-size-xs);
        line-height: var(--uv-font-size-sm);
    }
}

.uv-tag {
    font-size: var(--uv-font-size-xs);
    background-color: var(--uv-color-gray-light);
    padding: 4px 8px;
    border-radius: 2px;
    margin: 2px;
    line-height: 16px;
}

.task-selection {
    width: 112px;
    text-align: center;

    &:hover {
        .task-selection-button {
            background-color: var(--uv-color-gray-light);
            border: 2px solid var(--uv-color-gray-light);
            cursor: pointer;

            ::ng-deep {
                & > * {
                    color: var(--uv-color-primary);
                }
            }
        }
        p {
            color: var(--uv-color-primary)
        }
    }

    &-button {
        border: 2px solid var(--uv-color-gray-2);
        padding: 1.5rem;
        border-radius: 50%;
    
        &-selected {
            background-color: var(--uv-color-gray-light);
            border: 2px solid var(--uv-color-gray-light);
            cursor: pointer;

            ::ng-deep {
                & > * {
                    color: var(--uv-color-primary);
                }
            }
        }
    }

    &-selected {
        color: var(--uv-color-primary)
    }
}
